<script>import { clamp } from "./util";
export let value;
export let max;
export let color;
export let handleColor = void 0;
export let style;
export let onInput = () => {
};
let parent;
function pickPos(clientY) {
  const rect = parent.getBoundingClientRect();
  const y = clientY - rect.top;
  const percentage = y / rect.height;
  value = clamp(0, max, percentage * max);
  onInput(value);
}
function onMouse(e) {
  if (mouseHold && e.target instanceof HTMLElement) {
    pickPos(e.clientY);
  }
}
let mouseHold = false;
function mouseDown(e) {
  if (e.buttons === 1) {
    mouseHold = true;
    pickPos(e.clientY);
  }
}
function mouseUp() {
  mouseHold = false;
}
let touching = false;
function onTouch(e) {
  if (touching) {
    pickPos(e.touches[0].clientY);
  }
}
function touchStart(e) {
  if (e.touches.length === 1) {
    touching = true;
    pickPos(e.touches[0].clientY);
  }
}
function touchEnd() {
  touching = false;
}
</script>

<svelte:window
  on:mousemove={onMouse}
  on:mouseup={mouseUp}
  on:touchmove={onTouch}
  on:touchend={touchEnd}
/>

<div
  bind:this={parent}
  class="slider"
  on:mousedown={mouseDown}
  on:touchstart|preventDefault={touchStart}
  class:hue={style === 'hue'}
  class:alpha={style === 'alpha'}
  style="--color:{color.toHexString()};"
>
  <div class="slider-track">
    <div class="slider-track-overlay" />
  </div>
  <div
    class="slider-handle"
    style:top={(value / max) * 100 + '%'}
    style:background-color={handleColor}
  />
</div>

<style>.slider {
  padding: 0rem 0.3rem;
  flex-shrink: 0;
  user-select: none;
  box-sizing: border-box;
  position: relative;
}

.slider-track {
  height: 100%;
  width: 0.5rem;
  border-radius: 4px;
}

.hue .slider-track {
  background: linear-gradient(hsl(0deg, 100%, 50%), hsl(60deg, 100%, 50%), hsl(120deg, 100%, 50%), hsl(180deg, 100%, 50%), hsl(240deg, 100%, 50%), hsl(300deg, 100%, 50%), hsl(0deg, 100%, 50%));
}

.alpha .slider-track {
  background-image: repeating-conic-gradient(#bfbfbf 0 25%, #ffffff 0 50%);
  background-size: 0.5rem 0.5rem;
  background-position: 0 0, 0.25rem 0.25rem;
}

.alpha .slider-track-overlay {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom, transparent 0%, var(--color) 100%);
  border-radius: inherit;
}

.slider-handle {
  width: 1rem;
  height: 1rem;
  box-sizing: border-box;
  border-radius: 100px;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 3px 0px hsla(0deg, 0%, 0%, 0.5);
}</style>
